    60% { box-shadow: 0 0px 50px rgba(255, 255, 255, 1) }
    30% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.9) }
    70% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.7) }
    80% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.5) }
    90% { box-shadow: 0 0px 50px rgba(255, 255, 255, 0.3) }
